import React from 'react';
import { formatDate } from '../../utils/dateUtils';

/**
 * 캐릭터 데이터 세부 정보 표시 컴포넌트
 * @param {Object} props - 컴포넌트 props
 * @param {Object} props.data - 표시할 캐릭터 데이터
 * @param {Object} props.comparison - 비교 데이터 (선택적)
 * @returns {JSX.Element} - 데이터 세부 정보 컴포넌트
 */
const DataDetail = ({ data, comparison = null }) => {
  if (!data) return <div className="no-data">표시할 데이터가 없습니다.</div>;

  // 값의 변화를 표시하는 함수
  const renderChange = (newValue, oldValue) => {
    if (oldValue === undefined || newValue === oldValue) return null;
    
    const diff = typeof newValue === 'number' && typeof oldValue === 'number'
      ? newValue - oldValue
      : null;
      
    const isPositive = diff > 0;
    
    return (
      <span className={`change-value ${isPositive ? 'positive' : 'negative'}`}>
        {typeof oldValue === 'number' ? oldValue.toFixed(2) : oldValue} 
        {' → '} 
        {typeof newValue === 'number' ? newValue.toFixed(2) : newValue}
        {diff !== null && ` (${isPositive ? '+' : ''}${diff.toFixed(2)})`}
      </span>
    );
  };

  // 비교 데이터 존재 여부 확인
  const hasComparison = comparison !== null;

  return (
    <div className="data-detail-container">
      <div className="data-header">
        <h3>{data.charname} ({data.class})</h3>
        <p>
          서버: {data.server} | 관측 시간: {formatDate(data.observed_at)}
          {data.build && ` | 빌드: ${data.build}`}
        </p>
      </div>

      <div className="data-summary">
        <div className="summary-item">
          <div className="item-label">아이템 레벨</div>
          <div className="item-value">
            {data.temlv}
            {hasComparison && renderChange(data.temlv, comparison.temlv)}
          </div>
        </div>
        
        <div className="summary-item">
          <div className="item-label">환산 점수</div>
          <div className="item-value">
            {data.converted_zp.toFixed(2)}
            {hasComparison && renderChange(data.converted_zp, comparison.converted_zp)}
          </div>
        </div>
        
        {data.rank_total && (
          <div className="summary-item">
            <div className="item-label">전체 랭킹</div>
            <div className="item-value">{data.rank_total}</div>
          </div>
        )}
        
        {data.rank_class && (
          <div className="summary-item">
            <div className="item-label">클래스 랭킹</div>
            <div className="item-value">{data.rank_class}</div>
          </div>
        )}
      </div>

      <div className="data-details">
        <h4>환산 점수 상세</h4>
        <div className="details-grid">
          {data.receipt && Object.entries(data.receipt).map(([key, value]) => (
            <div key={key} className="detail-item">
              <div className="item-label">{key}</div>
              <div className="item-value">
                {typeof value === 'number' ? value.toFixed(2) : value}%
                {hasComparison && comparison.receipt && renderChange(value, comparison.receipt[key])}
              </div>
            </div>
          ))}
        </div>
      </div>

      <div className="data-equipment">
        <h4>장비 정보</h4>
        {data.display && data.display.equipment && (
          <div className="equipment-grid">
            {Object.entries(data.display.equipment).map(([slot, item]) => (
              <div key={slot} className="equipment-item">
                <div className="equipment-header">
                  <div className="slot-name">{slot}</div>
                  <div className="item-name">{item.Name}</div>
                </div>
                <div className="equipment-details">
                  {item.Grade && <div>등급: {item.Grade}</div>}
                  {item.품질 && <div>품질: {item.품질}</div>}
                  {item.재련 && <div>재련: +{item.재련}</div>}
                  {item.상급재련 && <div>상급 재련: +{item.상급재련}</div>}
                  {item.초월 && Array.isArray(item.초월) && (
                    <div>초월: {item.초월[0]}단계 {item.초월[1]}등급</div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="data-gems">
        <h4>보석 정보</h4>
        {data.display && data.display.gem && (
          <div className="gems-grid">
            {data.display.gem.map((gem, index) => (
              <div key={index} className="gem-item">
                <div className="gem-level">{gem[1]}레벨</div>
                <div className="gem-info">
                  <div>{gem[2]} {gem[4]}</div>
                  <div>{gem[5]}</div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="data-engravings">
        <h4>각인 정보</h4>
        {data.display && data.display.engr && (
          <div className="engravings-list">
            {data.display.engr.map((engr, index) => (
              <div key={index} className="engraving-item">
                <div className="engraving-name">{engr[0]}</div>
                <div className="engraving-level">
                  {engr[1][0] > 0 && `장착 ${engr[1][0]}`}
                  {engr[1][1] > 0 && ` 악세 ${engr[1][1]}`}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="data-cards">
        <h4>카드 정보</h4>
        {data.display && data.display.card && (
          <div className="cards-list">
            {Object.entries(data.display.card).map(([cardName, level], index) => (
              <div key={index} className="card-item">
                <div className="card-name">{cardName}</div>
                <div className="card-level">{level}각</div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default DataDetail;
