import { ref, get, query, orderByChild, equalTo } from 'firebase/database';
import { database } from '../firebase/config';

/**
 * 캐릭터 이름으로 Firebase에서 데이터를 검색하는 함수
 * @param {string} characterName - 검색할 캐릭터 이름
 * @returns {Promise<Array>} - 검색 결과 배열
 */
export const searchCharacterData = async (characterName) => {
  try {
    // 현재 Firebase 규칙에 맞게 경로를 수정
    // 서버 목록을 하드코딩하거나 설정에서 가져올 수 있습니다
    const servers = ['카제로스', '아브렐슈드', '카단', '니나브', '실리안', '아만'];
    
    let results = [];
    
    // 모든 서버에서 캐릭터 이름 검색
    for (const server of servers) {
      const characterPath = `characters/${server}/${characterName}`;
      const characterRef = ref(database, characterPath);
      
      try {
        const snapshot = await get(characterRef);
        
        if (snapshot.exists()) {
          // 데이터가 단일 객체이므로 배열로 변환하여 저장
          const data = snapshot.val();
          
          // 날짜별로 분리된 데이터인 경우
          if (typeof data === 'object' && !Array.isArray(data)) {
            Object.entries(data).forEach(([dateKey, dateData]) => {
              results.push({
                id: dateKey,
                server,
                charname: characterName,
                ...dateData
              });
            });
          } else {
            // 단일 데이터인 경우
            results.push({
              id: server + '_' + characterName,
              server,
              charname: characterName,
              ...data
            });
          }
        }
      } catch (err) {
        console.log(`${server} 서버에서 검색 중 오류:`, err);
        // 특정 서버 검색 오류는 건너뛰고 계속 진행
      }
    }
    
    // 날짜 기준으로 정렬
    return results.sort((a, b) => 
      new Date(b.observed_at) - new Date(a.observed_at)
    );
  } catch (error) {
    console.error('데이터 검색 오류:', error);
    throw error;
  }
};

/**
 * 점수 변화 데이터를 계산하는 함수
 * @param {Array} data - 캐릭터 데이터 배열
 * @returns {Array} - 날짜별 점수 변화 데이터
 */
export const calculateScoreChanges = (data) => {
  if (!data || data.length === 0) return [];
  
  return data.map((item, index) => {
    const prevItem = index < data.length - 1 ? data[index + 1] : null;
    
    return {
      id: item.id,
      date: new Date(item.observed_at),
      score: item.converted_zp || 0,
      scoreChange: prevItem 
        ? (item.converted_zp || 0) - (prevItem.converted_zp || 0) 
        : 0,
      data: item
    };
  });
};

/**
 * 두 데이터 사이의 변경사항을 계산하는 함수
 * @param {Object} newData - 새 데이터
 * @param {Object} oldData - 이전 데이터
 * @returns {Object} - 변경사항 객체
 */
export const calculateChanges = (newData, oldData) => {
  if (!newData || !oldData) return {};

  const changes = {};
  
  // 환산 점수 변화
  if (newData.converted_zp !== oldData.converted_zp) {
    changes.scoreChange = {
      old: oldData.converted_zp,
      new: newData.converted_zp,
      diff: newData.converted_zp - oldData.converted_zp
    };
  }
  
  // 아이템 레벨 변화
  if (newData.temlv !== oldData.temlv) {
    changes.itemLevel = {
      old: oldData.temlv,
      new: newData.temlv,
      diff: newData.temlv - oldData.temlv
    };
  }
  
  // 영수증 항목 변화
  if (newData.receipt && oldData.receipt) {
    changes.receipt = {};
    Object.keys(newData.receipt).forEach(key => {
      if (newData.receipt[key] !== oldData.receipt[key]) {
        changes.receipt[key] = {
          old: oldData.receipt[key],
          new: newData.receipt[key],
          diff: newData.receipt[key] - oldData.receipt[key]
        };
      }
    });
  }
  
  // 기타 변화사항을 필요에 따라 추가
  
  return changes;
};
